import React from "react";
import Page from "../components/page/Page";
import Header from "../components/header/Header";
import MainContent from "../components/mainContent/MainContent";
import Footer from "../components/footer/Footer";
import {client} from "../prismic-configuration";
import Prismic from "prismic-javascript";
import {RichText} from "prismic-reactjs";
import htmlSerializer from "../utils/htmlSerializer";

export default class Text extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            page: null
        };
    }

    componentDidMount() {
        client.query([
            Prismic.Predicates.at('document.type', 'about_us')
        ], {
            pageSize : 100,
            page : 1
        }).then(response => {

            response.results.forEach((value, index) => {
                if(index === 0) {
                    this.setState({
                        page: value
                    });
                }
            });

        });
    }

    render() {
        const title = this.state.page ? RichText.asText(this.state.page.data.title) : "";
        const text = this.state.page ? <RichText render={this.state.page.data.content} htmlSerializer={htmlSerializer} /> : "";
        const image = this.state.page ? <img src={this.state.page.data.image.url} alt="" className="b-page__icon" /> : "";

        return (
            <Page>
                <Header />
                <MainContent>
                    <div className="container">
                        <div className="row row_flex row_ac">
                            <div className="col-xs-12 col-md-6">
                                <h1 className="b-page__title">{title}</h1>
                                <div className="b-text">
                                    {text}
                                </div>
                            </div>
                            <div className="col-xs-12 col-md-4 col-md-offset-2 hidden-sm hidden-xs">
                                {image}
                            </div>
                        </div>
                    </div>
                </MainContent>
                <Footer />
            </Page>
        );
    }
}